import { Component, Vue } from "vue-property-decorator";
import firebaseAuthService from "@/services/FirebaseAuthService";
import MobileVerify from "@/components/otp/MobileVerify.vue";
import LoginImageInfo from "@/dto/LoginImageInfo";
import { FirestoreService } from "@/services/FirestoreService";


@Component({
  components: {
    MobileVerify
  }
})
export default class Register extends Vue {
  registerError = "";
  showError = false;
  fullName = "";
  email = "";
  password = "";
  repeatedPassword = "";
  loading = false;
  registered = false;
  mobileValid = true;
  emailValid = false;
  loginImageInfo: LoginImageInfo = new LoginImageInfo();
  async mounted() { await this.getLoginImage() }
  async getLoginImage() {
    try {
      const snapshot = await FirestoreService.getSingleDocument<LoginImageInfo>(
        "HomeScreen",
        "loginImage",
        LoginImageInfo.converter
      );
      if (snapshot !== null) {
        this.loginImageInfo = snapshot as LoginImageInfo;
      }
    } catch (error) {
      Vue.toasted.show("Error fetching GiftCards Details", {
        icon: "face",
        type: "error"
      });
      console.log(error);
    }
  }
  async register() {
    try {
      this.loading = true;
      if (this.validateFields()) {
        const signInResponse = await firebaseAuthService.createUserWithEmailAndPassword(
          this.email,
          this.password
        );
        // const emailResponse = await firebaseAuthService.sendVerificationMail();
        this.registered = true;
      }
    } catch (error) {
      this.registerError = error.message;
      this.showError = true;
    } finally {
      this.loading = false;
    }
  }

  validateFields() {
    const validationResult =
      this.validateEmail(this.email) === true
        ? this.validatePassword(this.password, this.repeatedPassword) === true
          ? true
          : false
        : false;
    if (validationResult) {
      this.registerError = "";
      this.showError = false;
    }
    return validationResult;
  }
  validateEmail(email: string) {
    const validateEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
      email.toLowerCase()
    );
    if (!validateEmail) {
      this.registerError = "Enter Valid Email Address";
      this.showError = true;
    }
    return validateEmail;
  }

  validatePassword(password: string, repeatPassword: string) {
    const notEmptyPassword = password.length > 0 ? true : false;
    const validatePassword = password.length >= 8 ? true : false;

    if (!notEmptyPassword) {
      this.registerError = "Password can not be empty";
      this.showError = true;
      return false;
    }
    if (!validatePassword) {
      this.registerError = "Password should be atleast 8 characters long";
      this.showError = true;
      return false;
    }
    if (repeatPassword !== password) {
      this.registerError = "The passwords must match, repeat password";
      this.showError = true;
      return false;
    }
    return true;
  }
}
